.wrapper{
    height: 30vh;
    background: cornflowerblue;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.content{
    padding: 10px;
    height: 100px;
    min-width: 40%;
    border: 1px solid;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
}